.logout-header {
    position: relative;
    height: 40px;
    background:rgba(109,165,230,1);
}

.login-teacher {
    font-size: 15px;
    font-family:Source Han Sans SC;
    position: absolute;
    right: calc(15vw + 80px);
    top: 10px;
    color:rgba(255,255,255,1);
}

.login-border {
    position: absolute;
    right: calc(15vw + 68px);
    top: 12px;
    height: 15px;
    border: 1px solid rgba(255,255,255,1);
}

.login-student {
    font-size: 15px;
    font-family:Source Han Sans SC;
    position: absolute;
    right: 15vw;
    top: 10px;
    color:rgba(255,255,255,1);
}

.cc {
    position: absolute;
    width: 180px;
    left: 1vw;
}