.login-article {
    position: relative;
    width: 100vw;
    height: calc(100vh - 100px);
}

.login-title {
    width: 40vw;
    left: 30vw;
    position: absolute;
    top: 10vh;
}

.login-username {
    position: absolute;
    top: 40vh;
    left: calc(50vw - 180px);
    width: 360px;
    height: 35px;
    background:rgba(255,255,255,1);
    border: 2px solid rgba(109,165,230,1);
    border-radius:10px;
}

.login-username-title {
    position: absolute;
    top: -18px;
    left: 5px;

    font-size: 20px;
}

.login-username-border {
    position: absolute;
    left: 50px;
    top: 4px;
    width:0px;
    height: 25px;
    border: 1px solid rgba(109,165,230,1);
    opacity:1;
}

.login-username-input {
    position: absolute;
    resize: none;
    left: 60px;
    width: 295px;
    height: 32px;
    /* top: 4px; */
    font-size: 20px;
    background: transparent;
    border: 0px;
}

.login-password {
    position: absolute;
    top: 52vh;
    left: calc(50vw - 180px);
    width: 360px;
    height: 35px;
    background:rgba(255,255,255,1);
    border: 2px solid rgba(109,165,230,1);
    border-radius:10px;
}

.login-password-title {
    position: absolute;
    top: -18px;
    left: 5px;

    font-size: 20px;
}

.login-password-border {
    position: absolute;
    left: 50px;
    top: 4px;
    width:0px;
    height: 25px;
    border: 1px solid rgba(109,165,230,1);
    opacity:1;
}

.login-password-input {
    position: absolute;
    resize: none;
    left: 60px;
    width: 295px;
    height: 32px;
    /* top: 4px; */
    font-size: 20px;
    background: transparent;
    border: 0px;
}

.login-login {
    position: absolute;
    width: 400px;
    top: 70vh;
    left: calc(50vw - 200px);
}
