.exam-article {
    position: relative;
    background:rgba(237,237,239,1);
    min-height: calc(100vh - 50px);
    width: 100vw;
    padding-top: 10px;
}

.exam-main {
    
    position: relative;
    width: 1000px;
    min-height: inherit;
    margin: 0 auto;
    background: white;
    display: table;
    /* column-count: 3;
    column-gap: 5px;
    column-rule: 5px black; */
}

.exam-left {
    vertical-align: top;
    width: 200px;
    display: table-cell;
}

.exam-left-aline {
    height: 30px;
}

.exam-left-img {
    margin: -2px;
    width: 205px;
}

.exam-middle {
    width: 20px;
    vertical-align: top;
    display: table-cell;
    /* height: 100vh; */
    background: rgba(109,165,230,1);
}

.exam-right {
    width: 780px;
    display: table-cell;
    vertical-align: top;
}

.exam-right-img {
    /* margin: -2px; */
    /* width: 780px; */
    width: inherit;
}

.exam-back {
    position: absolute;
    left: 20px;
    width: 50px;
    bottom: 10px;
}

.exam-right-bottom {
    background: rgba(250,250,250,1);
}

.exam-right-aline {
    background: rgba(250,250,250,1);
    height: 150px;
}

