.item-type {
    height: 50px;
    background:rgba(238,169,110,1);
    position: relative;
}

.item-arrow {
    width: 15px;
    position: absolute;
    transform: rotate(180deg);
    left: 8px;
    top: 10px;
}

.item-type-word {
    position: absolute;
    left: 30px;
    top: 3px;
    font-size: 22px;
    color: white;
    margin-top: 0px;
    padding: 5px;
}



.item-entry {
    display: table;
    padding: 10px;
}

.item-entry-left {
   display: table-cell;
   vertical-align: top; 
}

.item-entry-middle {
    display: table-cell;
    width: 100%;
}

.item-entry-right {
    display: table-cell;
    vertical-align: middle;
    width: 80px;
}


.item-left-point {
    border: 6px solid rgba(238,169,110,1);
    margin: 10px;
    border-radius: 50%;
}

.item-middle-word {
    margin: 0px;
    font-size: 20px;
    padding: 3px;
}

.item-middle-time {
    margin: 0px;
    font-size: 15px;
    padding: 3px;
}


.item-right-img {
    width: 25px;
    margin-left: 10px;
}

.item-right-box {
    width: 120px;
}

.item-right-openbox {
    width: 200px;
}

.item-aline {
    height: 5px;
    background-color:#E6E3E3;
}




.item-create {
    background: rgba(250,250,250,1);
    padding: 10px;
}

.item-create-entry {
    display: table;
    margin: 10px;
    vertical-align: top;
}

.item-create-entry-word {
    display: table-cell;
    vertical-align: top;
    font-size: 17px;
}

.item-create-entry-input {
    display: table-cell;
    margin-left: 10px;
    resize: none;
    background:rgba(255,255,255,1);
    border:1px solid rgba(204,204,204,1);
    opacity:1;
    border-radius:3px;
}

.item-create-name-input {
    width: 600px;
    height: 20px;
    font-size: 17px;
}

.item-create-log-input {
    width: 600px;
    height: 60px;
    font-size: 17px;
}

.item-create-log-input1 {
    width: 100px;
    height: 20px;
    font-size: 17px;
}

.item-create-log-input2 {
    width: 50px;
    height: 20px;
    font-size: 17px;
}

.item-create-log-input3 {
    width: 100px;
    height: 20px;
    font-size: 17px;
}

.item-create-log-input4 {
    width: 50px;
    height: 20px;
    font-size: 17px;
}

.item-create-publish {
    position: absolute;
    right: 80px;
    background: rgba(109,165,230,1);
    border: none;
    width: 50px;
    height: 30px;
    font-size: 15px;
    color: white;
}

.item-create-timed {
    position: absolute;
    width: 80px;
    right: 205px;
    height: 80px;
    top: 125px;
    font-size: 17px;
}

.item-create-timed-input {
    position: absolute;
    top: 15px;
    resize: none;
    width: 60px;
    left: 72px;
    height:     20px;
    font-size: 17px;
}