.cmBox {
    position: relative;
    width: 196px;
    height: 80px;
    border: 4px solid rgba(109,165,230,1);
    border-right: none;
    border-bottom-left-radius: 12px;
    text-align: center;
}

.cmBox-word {
    color: rgba(109,165,230,1);
    font-size: 23px;
    margin-top: 23px;
}

.none-text-decoration {
    text-decoration: none;
}

.cmBox-checked-img {
    width: 15px;
    position: absolute;
    left: 27px;
    top: 30px;
}